var OzToGramsRatio = 28.34952;
var GramsToOzRatio = 0.03527396195;
var OutputText = document.getElementById("outputText");
var OutputTextWholeUnits = document.getElementById("outputTextWholeUnits");
document.getElementById("ouncesToGram").addEventListener("click", function () { return ouncesToGram(); });
document.getElementById("gramToOunces").addEventListener("click", function () { return gramToOunces(); });
function ConvertGramToOunces() {
    var inputWeight = Number(document.getElementById("numberInput").value);
    return inputWeight * GramsToOzRatio;
}
function ConvertOuncesToGram() {
    var inputWeight = Number(document.getElementById("numberInput").value);
    return inputWeight * OzToGramsRatio;
}
function gramToOunces() {
    var result = ConvertGramToOunces();
    if (result > 0) {
        OutputText.innerHTML = result.toString() + " ounces";
        OutputTextWholeUnits.innerHTML = "(" + (result / 16).toFixed(2).toString() + " pounds)";
    }
}
function ouncesToGram() {
    var result = ConvertOuncesToGram();
    if (result > 0) {
        OutputText.innerHTML = result.toString() + " gram";
        OutputTextWholeUnits.innerHTML = "(" + (result / 1000).toFixed(2).toString() + " kilos)";
    }
}
